$lime-green: rgb(222 255 23 / 93%);
$deep-blue: rgb(77 0 255);
$peach: rgb(4, 0, 255);
$light-yellow: #ffff81;

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: $lime-green;
  font-family: balto, sans-serif;
  font-weight: 400;
  font-style: normal;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Form,
.Output {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  width: 85vw;
  height: fit-content;
  border: 0.5px solid black;
  margin-bottom: 100px;
}

.app-title {
  font-family: "Etroit";
  font-size: 5rem;
  padding-bottom: 2rem;
  padding-top: 1rem;
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  color: black;

  @media screen and (max-width: 450px) {
    font-size: 4rem;
  }
}

